import React, { Component } from 'react';
import './Container.css';
import ReactMarkdown from 'react-markdown';

class Container extends Component {
  projects() {
    const {
      projects
    } = this.props.company;
    
    const imgDescription = {
      marginBottom: '3rem'
    }

    if (projects) {
      return (
        projects.map((project, i) =>
          <div key={i} className="company--project">
            {project.screens.map((screen, index) =>
              <div key={index}>
                {screen.img ? 
                  <img src={screen.img} alt="" className="w-100"/>
                : ''}
                {screen.video ?
                  <video width="400" controls>
                    <source src={screen.video} type="video/mov" />
                    Your browser does not support HTML5 video.
                  </video>
                : ''}
                <p style={imgDescription}>
                  {screen.body}
                </p>
              </div>
            )}
          </div>
        )
      )
    }
    
    return;
  }

  render() {
    const {
      orientation,
      title,
      body,
      type,
      avatar,
      projects,
      dates
    } = this.props.company;
    const containerClasses = ['container'];

    if (orientation) {
      containerClasses.push(orientation);
    }
    
    containerClasses.push(type)

    return (
      <div className={containerClasses.join(' ')}>
        <div className="content">
          {avatar ? <img alt="Avatar" src={avatar} className="avatar" /> : ''}
          <h2>{title}</h2>
          
          <ReactMarkdown
            source={body}
            escapeHtml={false}
            linkTarget="_blank"
          />

          <div>{dates}</div>
          {projects ? this.projects() : ''}
        </div>
      </div>
    );
  }
}

export default Container;
